#circle-container {
    /* width:  calc(var(--circle-size)*2.1); */
    /* height: calc(var(--circle-size)*2.1); */
    /* border: 10px solid teal; */

    position: relative;
    width: var(--circle-size);
}

#circle-container:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

#circle-tech {
    transform: translateX(40%);

    justify-content: center;
    align-items: flex-start;
}

#circle-people {
    transform: translateY(40%);

    justify-content: flex-start;
    align-items: center;
}

#circle-experience {
    transform: translateX(80%) translateY(40%);

    justify-content: flex-end;
    align-items: center;
}

#circle-space {
    transform: translateX(40%) translateY(80%);

    justify-content: center;
    align-items: flex-end;
}

.circle {
    position: absolute;
    height: 50%;
    width: 50%;

    border-radius: 50%;

    border: 2px solid #000000;
    padding: var(--circle-text-padding);
    display: inline-flex;

    font-family: Noto Serif;
    font-style: normal;
    font-weight: normal;
    font-size: var(--circle-text-size);
    color: #20252E;
}

#expertise-text {
    flex-grow: 4;
